import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';

@Injectable({
  providedIn: 'root'
})
export class DatabaseService {

  constructor(public _fire : AngularFirestore) { }

   //add student
   AddStudents(data){
    return this._fire.collection('Students').add(data).then( results =>{
      console.log('successfully registered')
    }).catch ( err => {
      console.log('student is already registered :', err)
    });
  }
  //get data from firestore

  GetStudents(){
    return this._fire.collection('Students');
  }

  //function delete students
  DeleteStudents(ref){
    return this._fire.collection('Students').doc(ref).delete().then( results =>{
      console.log('successfully deleted')
    }).catch ( err => {
      console.log('erroe occured :', err)
    });
  }
}
